@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Satisfy&display=swap");
@keyframes fadeUp {
  0% {
    transform: translateY(5rem);
    opacity: 0;
  }
}
* {
  font-family: "Poppins", sans-serif;
  text-transform: capitalize;
  transition: all 0.2s linear;
  text-decoration: none;
  border: none;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 8.5rem;
}
html::-webkit-scrollbar {
  width: 1rem;
}
html::-webkit-scrollbar-track {
  background: #fff;
}
html::-webkit-scrollbar-thumb {
  background: #27ae60;
  border-radius: 5rem;
}

body {
  background: #eee;
}

input:focus, textarea:focus, select:focus {
  outline: none !important;
  border: none;
}

section {
  padding: 2rem 9%;
}

.section {
  position: fixed;
  top: 8.5rem;
  left: 0;
  right: 0;
  height: calc(100vh - 8.5rem);
  background: #fff;
  z-index: 1000;
}
.section__title {
  font-size: 2.5rem;
  padding: 1rem;
  color: #130f40;
  border-bottom: 0.1rem solid rgba(0, 0, 0, 0.2);
  text-align: center;
  margin: 0;
}
.section__wrapper {
  border: 0.1rem solid rgba(0, 0, 0, 0.2);
  border-radius: 0.5rem;
  animation: fadeUp 0.4s linear;
}
.section__content {
  flex: 1 1 41rem;
}
.section__content span {
  font-size: 2rem;
  color: #27ae60;
}
.section__content h3 {
  font-size: 4rem;
  color: #130f40;
  padding-top: 1rem;
}
.section__content p {
  font-size: 1.4rem;
  color: #666;
  line-height: 2;
  padding: 1rem 0;
}
.section__image {
  flex: 1 1 41rem;
  margin: 2rem 0;
  pointer-events: none;
}

.section__header {
  text-align: center;
  padding-bottom: 2rem;
}
.section__header span {
  font-family: "Satisfy", cursive;
  font-size: 3rem;
  color: #27ae60;
}
.section__header h3 {
  font-size: 3rem;
  color: #130f40;
}

#menu-btn {
  display: none;
}

.btn {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.7rem 1.8rem;
  font-size: 1.7rem;
  cursor: pointer;
  color: #fff;
  background: #27ae60;
  border-radius: 0.5rem;
}
.btn:hover {
  background: #130f40;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem 9%;
  background: #fff;
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
}
.header__logo {
  font-size: 2.5rem;
  font-weight: bolder;
  color: #130f40;
}
.header__logo i {
  padding-right: 0.5rem;
  color: #27ae60;
}
.header .navbar a {
  font-size: 1.7rem;
  margin: 0 1rem;
  color: #666;
}
.header .navbar a:hover {
  color: #27ae60;
}
.header__icons div {
  height: 4.5rem;
  width: 4.5rem;
  line-height: 4.5rem;
  border-radius: 0.5rem;
  background: #f7f7f7;
  color: #130f40;
  font-size: 2rem;
  margin-left: 0.3rem;
  text-align: center;
  cursor: pointer;
}
.header__icons div:hover {
  background-color: #27ae60;
  color: #fff;
}

.search-form {
  display: none;
}
.search-form form {
  height: 7rem;
  width: 100%;
  border-bottom: 0.2rem solid #130f40;
  display: flex;
  align-items: center;
  animation: fadeUp 0.4s linear;
}
.search-form form input {
  height: 100%;
  width: 100%;
  text-transform: none;
  font-size: 1.7rem;
  color: #130f40;
  padding-right: 1rem;
}
.search-form form label {
  font-size: 3rem;
  padding-right: 1.5rem;
  color: #130f40;
  cursor: pointer;
}
.search-form form label:hover {
  color: #27ae60;
}
.search-form.active {
  display: block;
}

.shopping-cart {
  overflow-y: scroll;
  padding-bottom: 8rem;
  display: none;
}
.shopping-cart::-webkit-scrollbar {
  width: 1rem;
}
.shopping-cart::-webkit-scrollbar-track {
  background: #fff;
}
.shopping-cart::-webkit-scrollbar-thumb {
  background: #130f40;
  border-radius: 5rem;
}
.shopping-cart .product__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 1.5rem;
  padding: 1.5rem;
}
.shopping-cart .product__wrapper .product {
  border-radius: 0.5rem;
  background: #f7f7f7;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 2rem;
  position: relative;
}
.shopping-cart .product__wrapper .product .fa-times {
  position: absolute;
  top: 0.7rem;
  right: 1rem;
  font-size: 2rem;
  cursor: pointer;
  color: #130f40;
}
.shopping-cart .product__wrapper .product .fa-times:hover {
  color: #27ae60;
}
.shopping-cart .product__wrapper .product img {
  height: 8rem;
}
.shopping-cart .product__wrapper .product .content h3 {
  font-size: 2rem;
  color: #130f40;
}
.shopping-cart .product__wrapper .product .content span {
  font-size: 1.5rem;
  color: #666;
}
.shopping-cart .product__wrapper .product .content span.price {
  color: #27ae60;
  font-size: 1.7rem;
}
.shopping-cart .product__wrapper .product .content input {
  width: 8rem;
  padding: 0.5rem 1.2rem;
  font-size: 1.5rem;
  color: #130f40;
  margin: 0.7rem 0;
}
.shopping-cart .cart-total {
  margin-top: 2rem;
  margin-bottom: 5rem;
  border: 0.1rem solid rgba(0, 0, 0, 0.2);
  border-radius: 0.5rem;
  animation: fadeUp 0.4s linear 0.4s backwards;
}
.shopping-cart .cart-total .total-info {
  padding: 1.5rem;
}
.shopping-cart .cart-total .total-info h3 {
  color: #130f40;
  font-size: 2rem;
  padding-bottom: 0.7rem;
}
.shopping-cart .cart-total .total-info span {
  color: #27ae60;
}
.shopping-cart.active {
  display: block;
}

.login-form {
  padding: 0 2rem;
  display: none;
}
.login-form form {
  margin: 2rem auto;
  max-width: 40rem;
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
  border: 0.1rem solid rgba(0, 0, 0, 0.2);
  padding: 2rem;
  border-radius: 0.5rem;
  animation: fadeUp 0.4s linear;
}
.login-form h3 {
  padding-bottom: 1rem;
  font-size: 2.5rem;
  text-transform: uppercase;
  color: #130f40;
}
.login-form .login-input {
  margin: 0.7rem 0;
  border-radius: 0.5rem;
  background: #f7f7f7;
  padding: 1rem 1.2rem;
  font-size: 1.6rem;
  color: #130f40;
  text-transform: none;
  width: 100%;
  box-sizing: border-box;
}
.login-form .remember {
  padding: 1rem 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.login-form .remember label {
  font-size: 1.5rem;
  cursor: pointer;
  color: #666;
}
.login-form .btn {
  margin: 1rem 0;
  width: 100%;
  text-align: center;
}
.login-form p {
  padding-top: 1rem;
  font-size: 1.5rem;
  color: #666;
}
.login-form p a {
  color: #27ae60;
}
.login-form p a:hover {
  color: #130f40;
  text-decoration: underline;
}
.login-form.active {
  display: block;
}

.home {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  gap: 2rem;
  position: relative;
  margin-top: 15rem;
}
.home__img {
  width: 100%;
  margin-right: 5rem;
}
.home__paralax {
  position: absolute;
  top: -18rem;
  right: -10rem;
  width: 80vw;
}

.category {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
  gap: 1.5rem;
  padding-bottom: 5rem;
}
.category__item {
  padding: 2rem;
  text-align: center;
  border-radius: 0.5rem;
  background: #f7f7f7;
}
.category__item img {
  height: 7rem;
}
.category__item h3 {
  font-size: 1.8rem;
  color: #130f40;
}
.category__item:hover {
  background: #27ae60;
}
.category__item:hover h3 {
  color: #fff;
}

.about {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2rem;
  background-color: #f7f7f7;
}
.about .image {
  flex: 1 1 40rem;
}
.about .image img {
  width: 100%;
}
.about .content {
  flex: 1 1 40rem;
}
.about .content span {
  font-family: "Satisfy", cursive;
  font-size: 3rem;
  color: #27ae60;
}
.about .content__title {
  font-size: 3rem;
  padding-top: 0.5rem;
  color: #130f40;
}
.about .content p {
  padding: 1rem 0;
  line-height: 2;
  font-size: 1.4rem;
  color: #666;
}
.about .content .icons__wrapper {
  margin-top: 2rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}
.about .content .icons__wrapper .icons {
  flex: 1 1 20rem;
  border-radius: 0.5rem;
  background: #fff;
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  gap: 2rem;
  padding: 2rem;
}
.about .content .icons__wrapper .icons h3 {
  font-size: 1.7rem;
  color: #130f40;
}

.popular__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  gap: 1.5rem;
}
.popular__item {
  border-radius: 0.5rem;
  position: relative;
  background: #f7f7f7;
  padding: 2rem;
  text-align: center;
}
.popular__item .fa-heart {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  font-size: 2rem;
  color: #666;
  cursor: pointer;
}
.popular__item .fa-heart:hover {
  color: #27ae60;
}
.popular__item .image {
  margin: 1rem 0;
}
.popular__item .image img {
  height: 15rem;
}
.popular__item .content h3 {
  font-size: 2rem;
  color: #130f40;
}
.popular__item .content .stars {
  padding: 1rem 0;
  font-size: 1.7rem;
}
.popular__item .content .stars i {
  color: gold;
}
.popular__item .content .stars span {
  color: #666;
}
.popular__item .content .price {
  font-size: 2rem;
  color: #130f40;
}
.popular__item .content .price span {
  font-size: 1.5rem;
  color: #666;
  text-decoration: line-through;
}

.banner .row-banner {
  background: url(../../build/img/row-banner.png) no-repeat;
  height: 45rem;
  background-size: cover;
  background-position: center;
  position: relative;
}
.banner .row-banner .content {
  position: absolute;
  top: 50%;
  left: 7%;
  transform: translateY(-50%);
}
.banner .row-banner .content span {
  font-family: "Satisfy", cursive;
  font-size: 4rem;
  color: #27ae60;
  color: #130f40;
}
.banner .row-banner .content h3 {
  font-size: 1.6rem;
  color: red;
  text-transform: uppercase;
}
.banner .row-banner .content p {
  font-size: 1.5rem;
  padding-bottom: 1rem;
}
.banner .grid-banner {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 1.5rem;
}
.banner .grid-banner .grid {
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
  height: 45rem;
}
.banner .grid-banner .grid:hover img {
  transform: scale(1.2);
}
.banner .grid-banner .grid img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.banner .grid-banner .grid .content {
  position: absolute;
  top: 2rem;
  padding: 0 2rem;
  z-index: 10;
}
.banner .grid-banner .grid .content.center {
  text-align: center;
  width: 100%;
}
.banner .grid-banner .grid .content.center span {
  color: #666;
}
.banner .grid-banner .grid .content.center h3 {
  color: #130f40;
}
.banner .grid-banner .grid .content span {
  font-size: 2.5rem;
  color: #fff;
}
.banner .grid-banner .grid .content h3 {
  font-size: 3rem;
  color: #fff;
  padding-top: 0.5rem;
}

.menu__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 1.5rem;
}
.menu__item {
  border-radius: 0.5rem;
  background: #f7f7f7;
  padding: 2rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.menu__item:hover {
  background: #130f40;
}
.menu__item:hover .content h3 {
  color: #fff;
}
.menu__item img {
  height: 10rem;
}
.menu__item .content h3 {
  font-size: 2rem;
  color: #130f40;
  padding-bottom: 0.5rem;
}
.menu__item .content .price {
  font-size: 1.8rem;
  color: #27ae60;
}

.order .icons {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.order .icons__item {
  border-radius: 0.5rem;
  padding: 2rem;
  text-align: center;
  background: #f7f7f7;
}
.order .icons__item img {
  height: 10rem;
}
.order .icons__item h3 {
  font-size: 2rem;
  color: #130f40;
  margin-top: 0.5rem;
}
.order .form {
  background: #f7f7f7;
  padding: 2rem;
  border-radius: 0.5rem;
}
.order .form__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.order .form__wrapper input, .order .form__wrapper textarea {
  width: 95%;
  padding: 1rem 1.2rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  text-transform: none;
  color: #130f40;
  margin-top: 0.5rem;
}
.order .form__wrapper textarea {
  height: 20rem;
  resize: none;
}
.order .form__wrapper span {
  font-size: 1.7rem;
  color: #666;
}
.order .form__wrapper .map {
  height: 100%;
  width: 100%;
  border-radius: 0.5rem;
}
.order .form__input {
  width: 49%;
  padding: 0.7rem 0;
}

.blog__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 1.5rem;
}
.blog__item {
  overflow: hidden;
  border-radius: 0.5rem;
}
.blog__item:hover .image img {
  transform: scale(1.2);
}
.blog__item:hover .image h3 {
  left: 1.5rem;
}
.blog__item .image {
  height: 25rem;
  overflow: hidden;
  width: 100%;
  position: relative;
}
.blog__item .image h3 {
  position: absolute;
  top: 1rem;
  left: -100%;
  border-radius: 0.5rem;
  background: #fff;
  color: #130f40;
  font-size: 1.5rem;
  padding: 0.5rem 1rem;
  z-index: 10;
}
.blog__item .image i {
  padding-right: 0.5rem;
  color: #27ae60;
}
.blog__item .image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.blog__item .content {
  padding: 1.5rem 2rem;
  background: #f7f7f7;
}
.blog__item .content .tags {
  padding-bottom: 1rem;
}
.blog__item .content .tags a {
  font-size: 1.5rem;
  color: #666;
}
.blog__item .content .tags a i {
  color: #27ae60;
}
.blog__item .content h3 {
  font-size: 2rem;
  color: #130f40;
}
.blog__item .content p {
  font-size: 1.4rem;
  color: #666;
  padding: 1rem 0;
  line-height: 2;
}

.footer {
  background: #f7f7f7;
  display: block;
  padding: 2rem 9%;
}
.footer .newsletter {
  text-align: center;
  margin-bottom: 2rem;
}
.footer .newsletter h3 {
  font-size: 2.5rem;
  color: #130f40;
  padding-bottom: 1rem;
}
.footer .newsletter form {
  margin: 1rem auto;
  max-width: 70rem;
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.footer .newsletter form input[type=email] {
  height: 100%;
  width: 100%;
  padding: 1rem 1.2rem;
  font-size: 1.6rem;
  color: #130f40;
  text-transform: none;
}
.footer .newsletter form input[type=submit] {
  padding: 0 2rem;
  font-size: 1.6rem;
  color: #fff;
  background: #27ae60;
  cursor: pointer;
}
.footer .newsletter form input[type=submit]:hover {
  background: #130f40;
}
.footer__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  gap: 1.5rem;
}
.footer__item h3 {
  font-size: 2.2rem;
  color: #130f40;
  padding: 1rem 0;
}
.footer__item a {
  font-size: 1.4rem;
  display: block;
  color: #666;
  padding: 1rem 0;
}
.footer__item a:hover {
  color: #27ae60;
}
.footer__item a:hover i {
  padding-right: 2rem;
}
.footer__item a i {
  padding-right: 0.5rem;
  color: #27ae60;
}
.footer__item p {
  font-size: 1.4rem;
  color: #666;
  padding: 1rem 0;
}
.footer .bottom {
  padding-top: 1rem;
  text-align: center;
}
.footer .bottom .share {
  margin: 1.5rem 0;
}
.footer .bottom .share a {
  height: 4.5rem;
  width: 4.5rem;
  line-height: 4.5rem;
  font-size: 2rem;
  border-radius: 0.5rem;
  margin: 0 0.3rem;
  color: #fff;
  background: #27ae60;
}
.footer .bottom .share a:hover {
  background: #130f40;
}
.footer .credit {
  font-size: 2rem;
  color: #666;
  padding: 1rem;
}
.footer .credit a {
  color: #27ae60;
}

@media (max-width: 1280px) {
  .home__paralax {
    top: -8rem;
    right: -5rem;
    width: 75%;
  }
}
@media (max-width: 1100px) {
  .home__paralax {
    top: 0rem;
    right: -5rem;
  }
}
@media (max-width: 991px) {
  html {
    font-size: 55%;
  }
  .header {
    padding: 2rem;
  }
  section {
    padding: 2rem;
  }
  .home {
    flex-wrap: wrap-reverse;
  }
  .home__paralax {
    top: -3rem;
    right: -5rem;
  }
}
@media (max-width: 774px) {
  #menu-btn {
    display: inline-block;
  }
  .header .navbar {
    position: absolute;
    top: 99%;
    left: 0;
    right: 0;
    background: #fff;
    border-top: 0.1rem solid rgba(0, 0, 0, 0.2);
    border-bottom: 0.1rem solid rgba(0, 0, 0, 0.2);
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    z-index: 1001;
  }
  .header .navbar.active {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  }
  .header .navbar a {
    font-size: 2rem;
    margin: 2rem;
    display: block;
  }
  .home__paralax {
    top: -4rem;
    right: 0;
    width: 130%;
  }
  .order .form__input {
    width: 100%;
  }
}
@media (max-width: 450px) {
  html {
    font-size: 50%;
  }
  .shopping-cart .total-info, .shopping-cart .product__wrapper {
    text-align: center;
  }
  .shopping-cart .btn {
    width: 100%;
    box-sizing: border-box;
  }
  .home .section__content h3 {
    font-size: 3rem;
  }
  .home .section__content p {
    font-size: 1.5rem;
  }
}